<template>
  <div class="surface-0 overflow-hidden py-4" style="min-height: 100vh;">
  <!-- <div class="overflow-hidden py-4"> -->
    <!-- <div class="py-3 relative my-3" style="min-height: 100vh;" id="hero"> -->
    <div class="pb-3 relative my-3" id="hero">
      <!-- <div class="mx-4 mt-0 w-100 text-center flex justify-content-center"> -->
      <div class="mx-4 mt-0 w-100 text-center">
        <div class="text-center mx-6">
          <img :src="$store.state.master.logo_app" style="width: 100px;" class="" alt="" srcset="">
        </div>
        <div class="">
          <h1 class="text-6xl font-bold text-gray-900 line-height-2 text-center mt-3 mb-0">PPID</h1>
          <h3 class="text-4xl font-bold text-gray-900 line-height-2 text-center mt-0 mb-3">Pejabat Pengelola Informasi dan Dokumentasi</h3>
        </div>
      </div>
    </div>
    <div class="grid mb-6">
      <div class="md:col-2"></div>
      <div class="col-12 md:col-8 grid card">
        <div class="grid ">
          <div class="col-6 md:col-6 sm:col-12 text-center">
            <img src="images/search.png" class="w-full" alt="" srcset="">
            <!-- <img src="images/logo-undip.png" class="text-center" style="height: 300px;" alt="" srcset=""> -->
          </div>
          <div class="col-6 md:col-6 sm:col-12 grid">
            <p class="text-2xl text-center mt-3">Untuk melihat detail laporan anda, silahkan masukkan kode dan type laporan yang anda miliki</p>
            <!-- <div class="col-12 md:col-12 flex flex-column gap-2 mt-1">
              <label for="type">Type</label>
              <Dropdown 
                v-model="dataForm.type" 
                :options="$store.state.data_static.jenis_kategori" 
                optionLabel="text" 
                optionValue="value" 
                placeholder="piilh asal pemohon" 
                class="w-full" 
                :disabled="loading" 
              />
            </div> -->
            <div class="col-12 md:col-12 flex flex-column gap-2 sm:my-0 xl:my-5">
              <label for="kode">Kode</label>
              <InputText 
                id="kode" 
                v-model="dataForm.kode" 
                aria-describedby="kode-help" 
                :disabled="loading" 
              />
            </div>
            <Button class="w-full mb-0 mt-3" label="Cari Laporan" @click="search()" :disabled="loading" />
          </div>
        </div>
      </div>
      <div class="md:col-2"></div>
      <div v-if="data.id" class="md:col-2"></div>
      <div v-if="data.id" class="col-12 md:col-8 grid mt-4 card">
        <div class="col-12 md:col-12 flex justify-content-between gap-2 mt-1">
          <h5 class="m-0 p-0 text-primary">
            {{data.jenis_kategori}}
            {{data['status_' + data.jenis_kategori] == 4 ? 'selesai' : 'sedang dalam proses'}}
          </h5>
          <h5 class="m-0 p-0 text-primary">{{$moment(data['tanggal_' + data.jenis_kategori]).format('LL')}}</h5>
        </div>
        <div v-if="data['kode_' + data.jenis_kategori + '_sebelumnya']" class="col-12 md:col-12 flex flex-column gap-2 mt-1">
          <label for="type">Kode Sebelumnya</label>
          <h5 class="m-0 p-0">{{data['kode_' + data.jenis_kategori + '_sebelumnya']}}</h5>
        </div>
        <div class="col-12 md:col-6 flex flex-column gap-2 mt-1">
          <label for="type">Asal</label>
          <h5 class="m-0 p-0">{{data.asal_pemohon}}</h5>
        </div>
        <div class="col-12 md:col-6 flex flex-column gap-2 mt-1">
          <label for="type">Kategori</label>
          <h5 class="m-0 p-0">{{data.nama_kategori}}</h5>
        </div>
        <div v-if="data.nama_biro" class="col-12 md:col-12 flex flex-column gap-2 mt-1">
          <label for="type">Biro</label>
          <h5 class="m-0 p-0">{{data.nama_biro}}</h5>
        </div>
        <div v-if="data.nama_fakultas" class="col-12 md:col-6 flex flex-column gap-2 mt-1">
          <label for="type">Fakultas</label>
          <h5 class="m-0 p-0">{{data.nama_fakultas}}</h5>
        </div>
        <div v-if="data.nama_prodi" class="col-12 md:col-6 flex flex-column gap-2 mt-1">
          <label for="type">Prodi</label>
          <h5 class="m-0 p-0">{{data.nama_prodi}}</h5>
        </div>
        <div class="col-12 md:col-6 flex flex-column gap-2 mt-1">
          <label for="type">Nama</label>
          <h5 class="m-0 p-0">{{data.nama_pemohon}}</h5>
        </div>
        <div class="col-12 md:col-6 flex flex-column gap-2 mt-1">
          <label for="type">Email</label>
          <h5 class="m-0 p-0">{{data.email_pemohon}}</h5>
        </div>
        <div class="col-12 md:col-6 flex flex-column gap-2 mt-1">
          <label for="type">No Identitas</label>
          <h5 class="m-0 p-0">{{data.no_identitas}}</h5>
        </div>
        <div class="col-12 md:col-6 flex flex-column gap-2 mt-1">
          <label for="type">No Handphone</label>
          <h5 class="m-0 p-0">{{data.no_hp_pemohon}} {{data.jenis_kategori}}</h5>
        </div>
        <div class="col-12 md:col-12 flex flex-column gap-2 mt-1">
          <label for="type">Deskripsi</label>
          <h5 class="m-0 p-0">{{data['deskripsi_' + data.jenis_kategori]}}</h5>
        </div>
        <div class="col-12 md:col-12 flex flex-column gap-2 mt-1">
          <label for="type">Foto</label>
          <Image class="text-left" :src="$store.state.ipBackend + '/' + data.foto_identitas" alt="Image" width="250" preview />
        </div>
        <div v-if="data.file_laporan1 || data.file_laporan2" class="col-12 md:col-12 flex flex-column gap-2 mt-1">
          <label for="username">File</label>
          <div v-if="data.file_laporan1" class="p-inputgroup flex-1">
            <Button label="open" @click="openNewTab(data.file_laporan1)"/>
            <InputText id="file_laporan1" v-model="data.file_laporan1" aria-describedby="file_laporan1-help" readonly/>
          </div>
          <div v-if="data.file_laporan2" class="p-inputgroup flex-1">
            <Button label="open" @click="openNewTab(data.file_laporan2)"/>
            <InputText id="file_laporan2" v-model="data.file_laporan2" aria-describedby="file_laporan2-help" readonly/>
          </div>
        </div>
      </div>
      <div v-if="data.id" class="md:col-2"></div>
      <div v-if="data.id" class="md:col-2"></div>
      <div v-if="data.id && data.catatan_manajer" class="col-12 md:col-8 grid mt-4 card">
          <!-- {{data}} -->
        <div v-if="setuju" class="col-12 md:col-12 text-right gap-2 mt-1">
          <h5 class="m-0 p-0 text-primary">{{setuju}}</h5>
        </div>
        <div class="col-12 md:col-12 flex flex-column gap-2 mt-1">
          <label for="type">Balasan</label>
          <h5 class="m-0 p-0">{{data.catatan_manajer}}</h5>
        </div>
        <div v-if="data.catatan_manajer && !setuju" class="p-buttonset w-full mt-3">
          <Button class="w-6" severity="success" label="Setuju" @click="pilihSetuju(1)" :disabled="loading || setuju" />
          <Button class="w-6" severity="danger" label="Tidak Setuju" @click="pilihSetuju(2)" :disabled="loading || setuju" />
        </div>
      </div>
      <div v-if="data.id" class="md:col-2"></div>
    </div>
  </div>
</template>

<script>
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
export default {
  setup () {
    return { v$: useVuelidate() }
  },
  data(){
    return {
			domain: window.location.origin,
			loading: false,
      dataForm: {
        kode: null,
        // type: null,
      },
      data: {},
    }
  },
  computed: {
    isValid() {
      return !this.v$.dataForm.$invalid;
    },
    setuju() {
      let status = this.data.is_setuju
      if(status === 1){
        return 'Disetujui'
      }else if(status === 2){
        return 'Tidak Disetujui'
      }else{
        return null
      }
    },
  },
  validations () {
    return {
      dataForm: {
        kode: { required },
        // type: { required },
      }
    }
  },
  async mounted() {
    const vm = this
    // console.log(vm.$route.query)
    // vm.dataForm.type = vm.$route.query.type || vm.$store.state.data_static.jenis_kategori[0].value
    vm.dataForm.kode = vm.$route.query.kode
    if(vm.isValid) vm.search()
  },
  methods: {
    openNewTab(path){
      window.open(this.$store.state.ipBackend + '/' + path)
    },
    async pilihSetuju(status){
      const vm = this
      const res = await vm.$axios.post(vm.data.jenis_kategori + '/update', {
        id: vm.data[vm.data.jenis_kategori + '_id'],
        is_setuju: status
      })
      if(res.data.status == 200){
        if(status === 1){
          vm.search()
        }else{
          vm.$router.push('/laporan?type=' + vm.data.jenis_kategori + '&kode=' + vm.data['kode_' + vm.dataForm.type])
        }
        vm.$toast.add({ severity: 'success', summary: 'Konfirmasi', detail: 'Berhasil Mengirim Persetujuan', life: 3000 });
      }else{
        vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Gagal Mengirim Persetujuan', life: 3000 });
      }
    },
    async search(){
      const vm = this
      if(vm.isValid){
        const res = await vm.$axios.post('gabungan/details_by_code', {kode: vm.dataForm.kode})
        // console.log('res', res)
        // const res = await vm.$axios.post(vm.dataForm.type + '/list', {['kode_' + vm.dataForm.type]: vm.dataForm.kode, halaman: 1, jumlah: 1})
        if(res.data.status == 200 && res.data.data.length >= 1){
          vm.data = res.data.data[0]
          vm.$toast.add({ severity: 'success', summary: 'Konfirmasi', detail: 'Data Ditemukan', life: 3000 });
        }else{
          vm.data = {}
          vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Data Tidak Ditemukan', life: 3000 });
        }
        console.log('res', res)
      }else{
        vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Data Tidak Valid', life: 3000 });
      }
    }
  },
}
</script>

